.root,html,body{
    margin: 0
}
body{
    
    font-family: 'Fira Sans', sans-serif;
    font-size: 100%;


}
nav{
    display: flex;
    justify-content: space-between;
    background-color: blueviolet;
    align-items: center;
    box-shadow: 0px 1px 10px 0px black;
    min-width:100%;
    color: white;
    height: 10%;
    
}
.logo{
    display: flex;
    align-items: center;
    margin-left: 3%;
}
.logo>img{
    width:2em;
    height: 80%;
}
.logo>h1{
    margin-left: 3%;
    width: 300px;
    font-size: 140%;
}
nav>h2{
    margin-right: 5%;
    font-weight: lighter;
    font-size: 140%;
}
.input{
    margin-top:5%;
    display: flex;
justify-content: space-evenly;
align-items: center;
}
.input>input{
    border: 1px solid grey;
    border-radius:5px;
    height: 45px;
    width:40%;
    font-size: larger;
    
}
.button{
    margin-top:5%;
    display: flex;
justify-content: center;
align-items: center;
}
button{
    width: 70%;
    height: 2em;
    background-color: blueviolet;
    color: white;
    font-size: larger;
    font-weight: bolder;
    
    border-radius: 5px;
    box-shadow: 0px 1px 5px 0px black;
}
.result{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
    position: relative;
    width: 70%;
    margin: auto;
    margin-top: 1.5%;
}
.result>img{
    max-width: 50%;

    border-radius: 5px;
    margin: auto;

}
.p1{
    position: absolute;
    top: 5%;
    width: 90%;
    text-align: center;
    font-size: 40px;
    color: white;
    text-shadow: 2px 0 0 #000, -2px 0 0 #000, 0 2px 0 #000, 0 -2px 0 #000, 2px 2px #000, -2px -2px 0 #000, 2px -2px 0 #000, -2px 2px 0 #000;

}
.p2{
    position: absolute;
    bottom: 5%;
    width: 90%;
    text-align: center;
    font-size: 40px;
    color: white;
    text-shadow: 2px 0 0 #000, -2px 0 0 #000, 0 2px 0 #000, 0 -2px 0 #000, 2px 2px #000, -2px -2px 0 #000, 2px -2px 0 #000, -2px 2px 0 #000;

}
button:hover{
    background-color: rgb(159, 86, 228);
}

@media (width <=1300px) {
    .result{
        width: 100%;

    }
    .result>img{
        width: 70%;
    }
    .p1{
        font-size: 30px;
    }
    .p2{
        font-size: 30px;
    }

    .input>input{

        height: 45px;
    }
  
    button{
        height: 50px;
    }
}